import React, { useEffect } from 'react';
import { AppBar, Toolbar, Typography, useScrollTrigger, Slide, Button } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import TwitterIcon from '@mui/icons-material/Twitter';
import PolicyIcon from '@mui/icons-material/Policy';
import { darken, lighten } from '@mui/system';
import ReactGA from 'react-ga';



import './App.css';
import { mainBackgroundColor, mainColor, selectedBackgroundColor } from './utils/commonStyle';
import TopTabs from './components/TopTabs';
import GachaPage from './components/GachaPage';
import { LinkButton } from './components/CustomButton';

ReactGA.initialize('G-PR4L22G5TW'); // トラッキングIDを置き換えます

function usePageTracking() {
  let location = useLocation();

  useEffect(() => {
    ReactGA.pageview(location.pathname + location.search);
  }, [location]);
}


const theme = createTheme({
  palette: {
    mode: 'dark',
    primary: {
      main: mainColor,
    },
    background: {
      default: mainBackgroundColor,
    },
    text: {
      primary: mainColor,
    },
  },
  // components: {
  //   MuiButton: {
  //     styleOverrides: {
  //       root: {
  //         color: mainColor,
  //         border: 1,
  //         borderColor: lighten(mainBackgroundColor, 0.5),
  //         '&.Mui-selected': {
  //           color: mainBackgroundColor,
  //           backgroundColor: darken(selectedBackgroundColor, 0.1),
  //           '&:hover': {
  //             backgroundColor: selectedBackgroundColor,
  //           },
  //         },
  //       },
  //     },
  //   },
  // },
});

const HideOnScroll = (props) => {
  const { children } = props;
  const trigger = useScrollTrigger();

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

const Footer = () => {
  return (
    <footer style={{
      padding: '4px 4px',
      borderTop: '1px solid #e7e7e7',
      textAlign: 'right',
      lineHeight: 1,
      color: mainColor,
      fontSize: '16px',
    }}>
      <div>画像の著作権は©コーエーテクモゲームス / Akatsuki Games Inc.に帰属します。</div>
      <div>©KOEI TECMO GAMES / Akatsuki Games Inc. All rights reserved. </div>
      <div>
        <LinkButton
          url="https://nohminist.github.io/"
          icon={<HomeIcon />}
        />
        <LinkButton
          url="https://twitter.com/nohminism"
          color={'#1DA1F2'}
          icon={<TwitterIcon />}
        />
        <LinkButton
          text="プライバシーポリシー"
          url="https://nohminist.github.io/privacy-policy/"
          bgcolor={darken('#7f7fff', 0.5)}
          icon={<PolicyIcon />}
        />
      </div>
      <div style={{ margin: '10px' }}>
        <Typography variant="body2" align="center">
          &copy; {new Date().getFullYear()} Nohminism
        </Typography>
      </div>
    </footer>
  );
}

const NavBarLink = ({ to, label }) => {
  const location = useLocation();
  const isActive = location.pathname === to;



  return (
    <Button
      component={RouterLink}
      to={to}
      sx={{
        border: 1,
        borderColor: lighten(mainBackgroundColor, 0.2),
        ...(isActive ? {
          backgroundColor: darken(mainColor, 0.2),
          '&:hover': {
            backgroundColor: darken(mainColor, 0.1),
          }
        } : {
        })
      }}

    >
      <span style={{
        fontWeight: 'bold',
        color: isActive ? mainBackgroundColor : mainColor,
      }}
      >
        {label}
      </span>
    </Button>
  );
};


const App = () => {
  const PageTracker = () => {
    let location = useLocation();

    useEffect(() => {
      ReactGA.pageview(location.pathname + location.search);
    }, [location]);

    // 何も描画しない
    return null;
  };

  return (
    <ThemeProvider theme={theme}>
      <Router>
        <PageTracker />
        <div style={{ backgroundColor: mainBackgroundColor, minHeight: '100vh', color: mainColor }}>
          <HideOnScroll>
            <AppBar position="fixed">
              <Toolbar>
                <Typography variant="h6" noWrap sx={{ flexGrow: 1, fontSize: '16px', fontWeight: 'bold' }}>
                  レスレリ便利ツール
                </Typography>
                <div style={{ margin: 2 }}>
                  <NavBarLink to="/" label="データ&調合" />
                </div>
                <div style={{ margin: 2 }}>
                  <NavBarLink to="/gacha" label="ガチャ" />
                </div>

              </Toolbar>
            </AppBar>
          </HideOnScroll>
          <Toolbar />
          <Routes>
            <Route path="/" element={<TopTabs />} />
            <Route path="/gacha" element={<GachaPage />} />
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
          <Footer />
        </div>
      </Router>
    </ThemeProvider>
  );
}


export default App;