export const DataIconCharacter = ({ name, dataView = 'grid' }) => {

  const el = character[name]
  const tileSize = dataView === 'gridL' ? 100 : 60;
  const iconSize = dataView === 'gridL' ? 24 : 20;

  return (
    <div
      style={{
        height: tileSize,
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
      }}
    >

      <Img name={name} square={tileSize} borderRadius={tileSize / 2} isDark={!el.got} />
      <div style={{ position: 'absolute', top: 0, left: 0 }}>
        <Img name={el.role} square={iconSize} isDark={!el.got} />
      </div>
      <div style={{ position: 'absolute', top: 0, right: 0 }}>
        <Img name={el.strongAttribute} square={iconSize} isDark={!el.got} />
      </div>
      <div style={{
        position: 'absolute',
        bottom: 0,
        left: '50%',
        transform: 'translateX(-50%)',
        fontWeight: 'bold',
        color: el.got ? 'orange' : 'lightgray',
        backgroundColor: 'rgba(0, 0, 0, 0.6)',
        textAlign: 'center',
        width: '100%',
        lineHeight: 1,
      }}
      >
        {dataView === 'gridL' &&
          <div style={{ fontSize: '14px' }}>
            {name.split('【')[0]}
          </div>
        }
        <div style={{ display: 'flex', width: '100px', overflow: 'hidden', height: '14px' }}>
          {[...Array(el.star)].map((_, index) => (
            <StarIcon key={index}
              style={{
                color: el.got ? 'yellow' : 'gray',
                fontSize: '14px',
                marginLeft: 0,
                marginRight: -4,
                marginTop: 0,
                marginBottom: -4,
              }}
            />
          ))}
          {dataView === 'gridL' &&
            <span style={{
              color: el.got ? mainColor : 'gray',
              fontSize: '12px',
              whiteSpace: 'nowrap',
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              maxWidth: 'calc(100% - 20px)', // StarIconの幅を差し引く
              marginLeft: '4px',
            }}>
              {name.split('【')[1].split('】')[0]}
            </span>
          }
        </div>
      </div>
    </div>
  )






}

export const DataIcon = ({ category, name, sortItem, dataView = 'grid' }) => {

  if (category === 'character') {
    return (
      <DataIconCharacter
        name={name}
        dataView={dataView}
      />
    )
  }

  if (category === 'memoria') {
    const el = memoria[name];

    return (
      <div
        style={{
          position: 'relative',
          display: 'flex',
          alignItems: 'center',
          width: '60px', // ここで必要な幅を設定
        }}

      >
        <Img name={name} width={60} isDark={!el.got} />
        <div style={{
          position: 'absolute',
          bottom: 0,
          left: '50%',
          transform: 'translateX(-50%)',
          fontWeight: 'bold',
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          textAlign: 'center',
          width: '60px',
          lineHeight: 1,
        }}>
          <div style={{
            display: 'block',
            color: el.got ? mainColor : 'gray',
            fontSize: '12px',
            whiteSpace: 'nowrap',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            maxWidth: '60px',
          }}>
            {el.name}
          </div>
          {sortItem !== 'date' &&
            <div style={{
              color: el.got ? 'orange' : 'gray',
            }}>
              {el.status[sortItem]}
            </div>
          }
        </div>
      </div>
    );
  }

  return <></>
}


export const CharName = ({ name }) => {
  return (
    <div>
      <span style={{ fontSize: '16px', fontWeight: 'bold' }}>
        {name.split('【')[0]}
      </span>
      <span style={{ fontSize: '12px' }} >
        {'【' + name.split('【')[1]}
      </span>
    </div>
  )
}

export const CharStatus = ({ name, sortItem, type }) => {

  const statsType = type === 'equipped' ? 'equippedStatus' : 'status'
  return (
    <div style={{ height: 20, display: 'flex' }}>
      {character[name][statsType] && Object.entries(character[name][statsType]).map(([key, value]) => {
        return (
          <CommonChart
            key={key}
            valueKey={key}
            value={value}
            category={'character'}
            isSortItem={sortItem === key}
            width={50}
            percentage={false}
          />
        )
      })}
    </div>
  )
}


const splitSortItem = (text) => {
  const result = text.match(/([a-zA-Z]+)(\d+)/);
  if (result) {
    const [, status, index] = result;
    return { status, index };
  }
  return { status: '', index: '' };
};
export const SkillStatusDitails = ({ skillName, skillStatus, sortItem, skillIndex }) => {

  let { status, index } = splitSortItem(sortItem)
  if (skillIndex === undefined) {
    status = sortItem
  }

  return (
    <div style={{ display: 'flex' }}>
      {skillStatus.map(key => {


        let isSortItem = false
        if (key === status && (skillIndex === undefined || skillIndex === Number(index))) {
          isSortItem = true
        }

        return (
          <CommonChart
            key={key}
            valueKey={key}
            value={skill[skillName][key]}
            category={skillIndex === undefined ? 'skill' : 'character'}
            isSortItem={isSortItem}
            width={50}
            percentage={key !== 'wait' && key !== 'currentWait'}
          />
        )
      })}
    </div>
  );
};

const skillParamsKeyCnv = {
  // 'damage':'currentDamage',
  // 'break':'currentBreak',
  // 'heal':'currentHeal',
  'wait': 'currentWait',
}

export const SkillParamsDitails = ({ charName, skillParamsKeys, sortItem, skillIndex }) => {
  let { status, index } = splitSortItem(sortItem)
  if (skillIndex === undefined) {
    status = sortItem
  }
  if (status === 'wait' && skillParamsKeys.includes('currentWait')) {
    status = skillParamsKeyCnv[status]

  }


  return (
    <div style={{ display: 'flex' }}>
      {skillParamsKeys.map(key => {



        let isSortItem = false
        if (key === status && (skillIndex === undefined || skillIndex === Number(index))) {
          isSortItem = true
        }



        return (
          <CommonChart
            key={key}
            valueKey={key}
            // value={skill[skillName][key]}
            value={character[charName].skillParams[skillIndex][key]}
            category={skillIndex === undefined ? 'skill' : 'character'}
            isSortItem={isSortItem}
            // width={(key === 'damage' || key === 'currentDamage') ? 70 : 50}
            width={50}
            // percentage={skillParamsType === 'origin' && (key === 'damage' || key === 'break' || key === 'heal')}　//暫定
            percentage={key === 'damage' || key === 'break' || key === 'heal'}
          />
        )
      })}
    </div>
  );
};


const skillAreaStyle = (isFilterItemArea) => {
  return {
    width: 20,
    minWidth: 20,
    height: 20,
    fontSize: '14px',
    textAlign: 'center',
    backgroundColor: isFilterItemArea ? darken(checkColor, 0.2) : 'transparent',  // 'default'を'transparent'に変更しました
  };
}
const skillParamsKeys = ['damage', 'break', 'heal', 'wait']
// const currnetSkillParamsKeys = ['currentDamage', 'currentBreak', 'currentHeal', 'currentWait']
const currnetSkillParamsKeys = ['damage', 'break', 'heal', 'currentWait']

export const SkillAttribute = ({ name, isFilterItem }) => {
  return (
    <div style={skillAreaStyle(isFilterItem)}>
      <Img name={name} square={20} />
    </div>
  )
}
export const SkillArea = ({ name, isFilterItem }) => {
  return (
    <div style={skillAreaStyle(isFilterItem)}>
      {name}
    </div>
  )
}
export const CharSkillDitails = ({ name, filterItems, sortItem, selectedCharacter, skillParamsType }) => {
  return (
    <>
      {character[name].skill.map((skillName, skillIndex) => {
        return (
          <div key={skillName}>
            <div style={{ width: 420, display: 'flex' }}>
              <Oval
                label={skillName}
                isChecked={filterItems?.skill.length > 0 && filterItems.skill.includes(skillName)}
                width={130}
              />
              <SkillAttribute
                name={skill[skillName].attribute}
                isFilterItem={filterItems.attribute.length > 0 && filterItems.attribute.includes(skill[skillName].attribute)}
              />
              <SkillArea
                name={skill[skillName].area}
                isFilterItem={filterItems.area.length > 0 && filterItems.area.includes(skill[skillName].area)}
              />
              <SkillParamsDitails
                charName={name}
                skillParamsKeys={skillParamsType === 'origin' ? skillParamsKeys : currnetSkillParamsKeys}
                sortItem={sortItem}
                skillIndex={skillIndex}
              />
            </div>
            <Collapse in={selectedCharacter === name}>
              <div style={{ fontSize: '13px', paddingLeft: '8px', lineHeight: 1.2, marginBottom: '4px' }}>
                <TextBr text={skill[skillName].text} />
              </div>
            </Collapse>
          </div>
        )
      })}
    </>
  )
} 