const CustomTabPanel = (props) => {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box>
          {children}
        </Box>
      )}
    </div>
  );
}


export const TopTabs = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [tabCategory, setTabCategory] = React.useState('character');
  const [anchorEl, setAnchorEl] = useState(null);

  const handleChange = (event, newValue) => {
    setTabCategory(newValue);
  };

  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const tabLabels = [
    { label: "キャラ", value: "character" },
    { label: "スキル", value: "skill" },
    { label: "メモリア", value: "memoria" },
    { label: "アイテム", value: "item" },
    { label: "素材", value: "material" },
    { label: "特性", value: "gift" },
    { label: "調合", value: "alchemy" },
    { label: "クエスト", value: "quest" },
  ];

  return (
    <Box sx={{ width: '100%' }}>
      <Grid container spacing={0.5} sx={{margin:'4px'}}>
        {tabLabels.map((tab, index) => (
          <Grid item key={index}>
            <Button
              onClick={() => { setTabCategory(tab.value); handleMenuClose(); }}
              // variant="contained"
              // size='small'
              sx={{
                // padding:0,
                color: mainColor,
                border: 1,

                borderColor: lighten(mainBackgroundColor, 0.5),
                '&.Mui-selected': {
                  color: mainBackgroundColor,
                  backgroundColor: darken(selectedBackgroundColor, 0.1),
                  '&:hover': {
                    backgroundColor: selectedBackgroundColor,
                  },
                },
                ...(tabCategory === tab.value ? {
                  color: mainBackgroundColor,
                  backgroundColor: darken(selectedBackgroundColor, 0.1),
                  '&:hover': {
                    backgroundColor: selectedBackgroundColor,
                  }
                } : {
                })
              }}
            >
              {tab.label}
            </Button>
          </Grid>
        ))}
        <Grid item>
          <LinkButton
            text="使い方"
            url="https://nohminist.github.io/category-resleri/2023/12/23/resleri-tool-features.html"
            bgcolor={darken('#7fff7f', 0.5)}
            icon={<HelpOutlineIcon />}
          />
        </Grid>
      </Grid>









      <div style={{ paddingLeft: '4px', fontSize: '14px' }}>
        ・2024/01/01_新キャラ・メモリア、イベント装備・素材を反映
      </div>

      {tabLabels.map((tab, index) => (
        <CustomTabPanel key={index} value={tabCategory} index={tab.value}>
          <TabContents category={tab.value} onChangeTab={setTabCategory} />
        </CustomTabPanel>
      ))}

      <ButtonToClearLocalStorage />
    </Box>
  );
}
export default TopTabs