import React from 'react';
import { ToggleButtonGroup, ToggleButton } from '@mui/material';
import ViewHeadlineIcon from '@mui/icons-material/ViewHeadline';
import ViewCompactIcon from '@mui/icons-material/ViewCompact';
import ViewModuleIcon from '@mui/icons-material/ViewModule';

import { toggleButtonStyle } from '../utils/commonStyle';


const valueMap = {
  'statusType': ['basic', 'equipped'],
  'skillParamsType': ['origin', 'calculated'],
  'charTileSize': ['small', 'large'],
  'characterView': ['line', 'grid', 'gridL'],
  'memoriaView': ['line', 'grid'],

}

const textMap = {
  'basic': '装備前ステータス',
  'equipped': '装備後ステータス',
  'origin': 'スキル値',
  'calculated': '計算値(実ウェイト)',
  'small': '小',
  'large': '大',
}

const iconMap = {
  'line': <ViewHeadlineIcon />,
  'grid': <ViewCompactIcon />,
  'gridL': <ViewModuleIcon />,
}

export const CommonToggleButtonGroup = ({ group, value, setValue, setTrigger }) => {

  const handleChange = (e, newValue) => {
    if (newValue) {
      setValue(newValue);
      localStorage.setItem(group, newValue);
      if (setTrigger) {
        setTrigger(prev => 1 - prev);
      }
    }
  };

  return (
    <ToggleButtonGroup
      value={value}
      exclusive
      onChange={handleChange}
      aria-label="status condition"
    >
      {valueMap[group].map(valueItem => {
        return (
          <ToggleButton
            key={valueItem}
            value={valueItem}
            aria-label={valueItem}
            sx={{ ...toggleButtonStyle, height: 20 }}
          >
            {iconMap[valueItem]
              ? iconMap[valueItem]
              : <span style={{ fontSize: '12px' }}>{textMap[valueItem]}</span>
            }
          </ToggleButton>
        )
      })}
    </ToggleButtonGroup>
  )
}