import React, { useState, useEffect } from 'react';
import { Badge, Button, Collapse, Chip, Grid, List, ListItem, ListSubheader, Stack, Tooltip } from '@mui/material';
import { darken, lighten } from '@mui/system';

import gift from '../constants/gift';
import { Img } from './Img'
import { mainBackgroundColor, checkColor, mainColor, giftColorMap } from '../utils/commonStyle';







export const Oval = ({ iconName, label, isChecked, isChecked1, isChecked2, isChecked3, width, colorMatched = true, isValid = true, fontSize }) => {
  const getColor = () => {
    if (!colorMatched) {
      return darken(mainColor, 0.4);
    }
    return null;
  }

  const getBackgroundColor = () => {
    if (!isValid) {
      return darken(mainBackgroundColor, 0.2);
    }
    if (isChecked || isChecked1) {
      return darken(checkColor, 0.2);
    }
    if (isChecked2) {
      return darken(checkColor, 0.4);
    } if (isChecked3) {
      return darken(checkColor, 0.6);
    }
    return lighten(mainBackgroundColor, 0.15);
  };


  if (width) {
    return (
      <div style={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start',
        color: getColor(),
        width: width ?? null,
        height: '18px',
        fontSize: fontSize ?? '13px',
        marginTop: '1px',
        marginBottom: '1px',
        paddingLeft: iconName ? '1px' : '8px',
        backgroundColor: getBackgroundColor(),
        borderRadius: '16px', // Chipの丸みを再現
        boxSizing: 'border-box', // パディングとボーダーを幅に含める
      }}>
        {iconName && <Img name={iconName} square={16} borderRadius={8} />}
        <span style={{
          marginLeft: iconName ? '1px' : 0,
          overflow: 'hidden',
          whiteSpace: 'nowrap',
          textOverflow: 'ellipsis',
          maxWidth: '100%', // 省略するために最大幅を設定
        }}>
          {label}
        </span>
      </div>
    )
  }



  return (
    <div style={{
      display: 'flex',
      alignItems: 'center',
      color: getColor(),
      height: '18px',
      fontSize: fontSize ?? '13px',
      marginTop: '1px',
      marginBottom: '1px',
      paddingLeft: iconName ? '1px' : '8px',
      paddingRight: '8px',
      backgroundColor: getBackgroundColor(),
      borderRadius: '16px', // Chipの丸みを再現
    }}>
      {iconName && <Img name={iconName} square={16} borderRadius={8} />}
      <span style={{
        marginLeft: iconName ? '1px' : 0,
      }}>
        {label}
      </span>
    </div>
  )
}
