import React, { useState, useEffect } from 'react';
import { ButtonBase, Box, Chip, Collapse, Grid, List, ListSubheader, ListItem, ListItemButton, Stack, Tooltip } from '@mui/material';
import { darken, lighten } from '@mui/system';


import { mainBackgroundColor, selectedBackgroundColor, checkColor, mainColor, toggleButtonStyle } from '../utils/commonStyle';
import { DataContents } from './ListItemContents'
import { DataIcon, CharName, CharStatus, CharSkillDitails, SkillAttribute, SkillArea, SkillStatusDitails } from './Parts';
import  character  from '../constants/character';
import memoria from '../constants/memoria';
import material from '../constants/material';
import { CommonToggleButtonGroup } from './CustomToggleButton';
import { Img } from './Img'
import { alchemy } from './TabContents';
import { updateAlchemy } from '../utils/alchemyUtils';




const dataMap = {
  character: character,
  memoria: memoria,
  material: material,
}


export const ListContents = ({ rows, category, filterItems, setFilterItems, sortItem, onChangeTab, setTrigger, statusType, skillParamsType }) => {

  const [selectedName, setSelectedName] = useState(null);

  const [got, setGot] = useState(
    dataMap[category]
      ? Object.entries(dataMap[category]).reduce((obj, [name, el]) => {
        obj[name] = el.got;
        return obj;
      }, {})
      : {} // dataMap[category] が undefined の場合は空のオブジェクトを使用
  );

  const [dataView, setDataView] = useState(() => {
    const storedData = localStorage.getItem(category + 'View');
    return storedData ?? 'line';
  });



  const handleRowClick = (name) => {
    setSelectedName(name === selectedName ? null : name);
  };




  const toggleGot = (name) => {

    //元データ更新
    dataMap[category][name].got = !dataMap[category][name].got

    //localStorage更新
    if (category === 'character' || category === 'memoria') {

      const storedData = JSON.parse(localStorage.getItem(name));
      storedData.got = dataMap[category][name].got;
      localStorage.setItem(name, JSON.stringify(storedData));

    } else if (category === 'material') {

      const storedMaterials = JSON.parse(localStorage.getItem('materials'));
      storedMaterials[name] = dataMap[category][name].got;
      localStorage.setItem('materials', JSON.stringify(storedMaterials));

    }

    //画面更新
    setGot(prev => ({ ...prev, [name]: !prev[name] }));

    //alchemyのelに影響するので更新
    if (category === 'character' || category === 'material') {
      updateAlchemy(alchemy)
    }

  };




  return (
    <>
      {(category === 'character' || category === 'memoria') &&
        <CommonToggleButtonGroup
          group={category + 'View'}
          value={dataView}
          setValue={setDataView}
        />
      }

      <Grid container spacing={dataView === 'line' ? 0 : '4px'} sx={{ marginTop: 1 }}>

        {rows.map((dataEl, index) => {
          return (
            <React.Fragment key={dataEl.name}>
              {dataView === 'line'
                ? <Grid item key={dataEl.name}
                  sx={{
                    color: mainColor,
                    borderTop: '1px solid gray',
                    width: '100%',
                    height: 'auto',
                    // cursor: 'default', // カーソルのスタイルを設定(onClickイベントが無ければカーソルdefault)
                    '&:hover': {
                      bgcolor: lighten(mainBackgroundColor, 0.05),
                    },
                  }}
                  onClick={() => handleRowClick(dataEl.name)}
                >
                  <DataContents
                    category={category}
                    name={dataEl.name}
                    filterItems={filterItems}
                    sortItem={sortItem}
                    toggleGot={toggleGot}
                    selectedName={selectedName}
                    onChangeTab={onChangeTab}

                    setTrigger={setTrigger}
                    statusType={statusType}
                    skillParamsType={skillParamsType}
                  />
                </Grid>
                : <Grid item key={dataEl.name}>
                  <ButtonBase
                    onClick={() => toggleGot(dataEl.name)}
                  >
                    <DataIcon
                      category={category}
                      name={dataEl.name}
                      isGet={got[dataEl.name]}//たぶん意味ない
                      sortItem={sortItem}
                      dataView={dataView}
                    />
                  </ButtonBase>
                </Grid>
              }
            </React.Fragment>
          )
        })}
      </Grid>
    </>
  )



}